<template lang="pug">
    .test__item-content
        .test__item-row
            .tests__number {{idx+1}}
            .tests__code  {{test.code}}
            .tests__subject {{test.group.subject.name}}
            .tests__class  {{test.group.groupClass}}
            .tests__class  {{test.duration}}
            .tests__pass
                button(@click.prevent="passAvailableTest()" id='passTest').tests__pass-button 
        .form__test(v-if="testVisible")
            .timer
                .remaining__time Осталось времени 
                .remaining__time-content {{hours}}:{{minutes}}:{{seconds}}
            form(@submit.prevent="submitTest()").new__test
                ul.new__test-list 
                    li.new__test-point(v-for="item,idx in testQuestions" :key="item.id")
                        new-test-point(
                            :question="item"
                            :index="idx"
                            :test='test'
                        )
                .tests__ready
                    button(type='submit').tests__ready-button Готово
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        newTestPoint: () => import("./new-test-point")
    },
    created() {
        if(this.test.id) this.fetchTestQuestions(this.test.id)
    },
    data() {
        return {
            testResults: {
                test: {
                    id: this.test.id
                },
                result: 0,
                scholar: {
                    id: 329
                }
            },
            testVisible: false,
            result: 0,
            local: '',
            currentTime: this.test.duration*60,
            hours: '',
            minutes: '',
            seconds: '',
            expired: true,
            timer: null
        }
    },
    computed: {
        ...mapState ("testQuestions", {
            testQuestions: state => state.testQuestions})
    },
    props: {
        test: Object,
        idx: Number,
        userId: Number
    },
    methods: {
        ...mapActions("testQuestions", ["fetchTestQuestions"]),
        ...mapActions("scholarTests", ["passTest"]),
        passAvailableTest() {
            this.$emit("passTestAvailable",{
                available: false
            })
            this.testVisible=!this.testVisible
            let buttons = document.getElementsByClassName('tests__pass-button')
            let navLinkGroups = document.querySelector('.nav__item-groups')
            let navLinkSchedule = document.querySelector('.nav__item-schedule')
            let navLinkProgrammes = document.querySelector('.nav__item-programmes')
            let navLinkTests = document.querySelector('.nav__item-tests')
            let headerLogo = document.querySelector('.header__logo')
            let userInfo = document.querySelector('.user-info')
            let filters = document.querySelectorAll('.tests__filter-select')
            let quitLink = document.querySelector('.quit-link')
            buttons.forEach(elem => {
                elem.disabled=true
            });
            filters.forEach(elem => {
                elem.disabled=true
            });
            navLinkGroups.style="cursor: not-allowed;pointer-events: none;"
            navLinkSchedule.style="cursor: not-allowed;pointer-events: none;"
            navLinkProgrammes.style="cursor: not-allowed;pointer-events: none;"
            navLinkTests.style="cursor: not-allowed;pointer-events: none;"
            headerLogo.style="cursor: not-allowed;pointer-events: none;"
            userInfo.style="cursor: not-allowed;pointer-events: none;"
            quitLink.style="cursor: not-allowed;pointer-events: none;"
            this.expired = false
            this.startTimer()
        },
        submitTest() {
            if (!this.expired)
                this.stopTimer()
            for(let i=0; i<this.testQuestions.length; i++) {
                this.local = JSON.parse(localStorage.getItem(i))
                if(this.local&&this.testQuestions[i].correctAnswer == this.local.answer) this.testResults.result++
                localStorage.removeItem(i);
            }
            this.passTest(this.testResults);
            console.log(this.testResults)
            this.testVisible=!this.testVisible
            console.log('Your result is ' + this.result)
            this.result=0
            var buttons = document.getElementsByClassName('tests__pass-button')
            buttons.forEach(elem => {
                elem.disabled=false
            });
            let navLinkGroups = document.querySelector('.nav__item-groups')
            let navLinkSchedule = document.querySelector('.nav__item-schedule')
            let navLinkProgrammes = document.querySelector('.nav__item-programmes')
            let navLinkTests = document.querySelector('.nav__item-tests')
            let headerLogo = document.querySelector('.header__logo')
            let userInfo = document.querySelector('.user-info')
            let filters = document.querySelectorAll('.tests__filter-select')
            let quitLink = document.querySelector('.quit-link')
            filters.forEach(elem => {
                elem.disabled=false
            });
            navLinkGroups.style="cursor: allowed;pointer-events: auto;"
            navLinkSchedule.style="cursor: allowed;pointer-events: auto;"
            navLinkProgrammes.style="cursor: allowed;pointer-events: auto;"
            navLinkTests.style="cursor: allowed;pointer-events: auto;"
            headerLogo.style="cursor: allowed;pointer-events: auto;"
            userInfo.style="cursor: allowed;pointer-events: auto;"
            quitLink.style="cursor: allowed;pointer-events: auto;"
            this.$emit("passTestAvailable",{
                available: true
            })
        },
        startTimer() {
            this.timer = setInterval(() => {
                this.currentTime--
                let thours = Math.floor(this.currentTime/(60*60))
                let tminutes = Math.floor(this.currentTime/60)
                let tseconds = this.currentTime - thours*60*60-tminutes*60
                this.hours = (thours < 10) ? '0' + thours : thours;
                this.minutes = (tminutes < 10) ? '0' + tminutes : tminutes;
                this.seconds = (tseconds < 10) ? '0' + tseconds : tseconds;
            }, 1000)
        },
        stopTimer() {
            if(this.expired)
                this.submitTest()
            clearTimeout(this.timer)
        }
    },
    watch: {
        currentTime(time) {
            if (time === 0) {
                this.expired = true
                this.stopTimer()
            }
        }
    }
}
</script>
