    border-radius: 5px;
    background: url(../../../assets/images/icons/assignment.svg);
    background-size: cover;
}
.tests__ready-button {
    color: #FFFAF0;
    background: #464549;
    border-radius: 5px;
    padding: 1%;
    font-family: "Alegreya Sans Extra Bold";
    &:hover{
        opacity: .5;
        transition: .5s;
        cursor: pointer;
    }
}
